// blocker
.blocker {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 100;
    padding: 1rem;
    box-sizing: border-box;
    background-color: rgb(0, 0, 0);
    background-color: rgba(0, 0, 0, 0.75);
    text-align: left;
    opacity: 0;
    animation: blocker-fade-in .25s forwards ease;
    @at-root {
        @keyframes blocker-fade-in {
            100% {
                opacity: 1
            }
        }
    }
    &:before {
        content: "";
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -0.05em;
    }
    &.behind {
        background-color: transparent;
    }
}

.modal {
    // overflow: auto;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    display: inline-block;
    vertical-align: middle;
    position: relative;
    z-index: 101;
    background: #fff;
    // padding: 2rem 1rem 1rem;
    // text-align: left;
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    opacity: 0;
    top: -5rem;
    animation: modal-drop-in 1s forwards .125s ease;
    @at-root {
        @keyframes modal-drop-in {
            100% {
                opacity: 1
            }
            100% {
                top: 0
            }
        }
    }
    // @include bp(lg) {
    //     max-width: calc(1200px - 2rem);
    //     margin:0 auto;
    // }
    a.close-modal {
        position: absolute;
        top: .25rem;
        right: .25rem;
        display: block;
        text-indent: -9999px;
        background: url("data:image/png;base64,") no-repeat 0 0;
        background-size: cover;
        @include bp(md) {
            top: 0;
            right: 0;
        }
    }
    .close-modal-wrapper {
        width: 3.125rem;
        height: 3.125rem;
        position: absolute;
        top: 0;
        right: 0;
        // background:red;
        @include bp(md) {
            height: 4.5rem;
            width: 4.5rem;
        }
        .close-modal-left,
        .close-modal-right {
            width: .125rem;
            height: 3.125rem;
            background: $color-text-secondary;
            position: absolute;
            transition: 150ms linear;
            @include bp(md) {
                height: 4.5rem;
            }
        }
        .close-modal-left {
            transform: rotate(-45deg);
            left: 1.5625rem;
            @include bp(md) {
                left: 2.25rem
            }
        }
        .close-modal-right {
            transform: rotate(45deg);
            left: 1.5625rem;
            @include bp(md) {
                left: 2.25rem
            }
        }
        &:hover {
            .close-modal-left,
            .close-modal-right {
                background: lighten($color-secondary, 20%)
            }
        }
        &:active {
            .close-modal-left,
            .close-modal-right {
                background: $color-secondary
            }
        }
    }    
}

.portfolio {
    .portfolio-title {
        // @include flex (null,
        // center);
        padding: .5rem 4.125rem .5rem .5rem;
        background: $color-primary;
        font-size: .875rem;
        @include bp(md) {
            min-height: 4.5rem;
            font-size: 1rem;
            padding: 1rem 5.5rem 1rem 1rem
        }
        h1 {
            // width:100%;
            color: $color-text-secondary;
            margin: 0;
            font-family: inherit;
            // background:blue;
            // line-height:1 !important;
        }
    }
    .portfolio-body {
        padding: 0 .5em .5em;
        @include bp(sm) {
            max-width: 768px;
            margin: 0 auto;
        }
        h2 {
            text-align: center;
            &:first-of-type {
                margin-top: 1em;
            }
            @include bp(md) {
                text-align: left;
            }
        }
        p {
            font-size: 1rem;
        }
        img {
            max-width: 100%;
            display: block;
            margin: 0 auto 1em;
        }
        .flex {
            @include bp(sm) {
                display: flex;
                flex-direction:row;
                justify-content:space-between;
                align-items:center;
                width: 100%;
                // background:red;
                overflow:hidden;
				img{
					margin:0 0 1em
				}
                &-2 {
                    img {
                        width:48%;
                    }
                }
                &-3 {
                    img {
                        width:31.33334%;
                    }
                }
            }
        }
    }
}

